body{
	padding: 50px;
}

@media (max-width: 400px) {
	body{
		padding: 25px;
	}
}

.defaultBtn{
	background-color: white;
	color: black;
	border: 1px solid black;
	/* border: none; */
	border-radius: 10px;
	font-size: 20px;
	padding: 10px;
	padding-left: 25px;
	padding-right: 25px;
	cursor: pointer;
	margin-top: 30px;
	transition: all 300ms;
	font-weight: bold;
}

.defaultBtn:hover{
	opacity: 0.5;
	transform: scale(1.1);
}

.center{
	display: flex;
	align-items: center;
	/* justify-content: center; */
	flex-direction: column;
	/* width: 100vw;
	height: 100vh; */
}

input[type="checkbox"]{
	cursor: pointer;
	margin-right: 10px;
	width: 20px;
	height: 20px;
}

.checkbox{
	margin: 10px;
	width: 175px;
	display: flex;
	align-items: center;
	margin-left: auto;
	margin-right: auto;
}

.checkbox label{
	text-align: center;
	/* width: 100%; */
}
.title{
	margin-top: 0;
}

.checkBoxGrid{
	/* display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center; */
	/* max-width: 600px; */


	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
	width: 100%;
	
	/* max-width */
}





.key{
	margin: 0;
}

.checkContainer{
	width: 100%;
	/* text-align: center; */
	margin: 25px;
	max-width: 800px;
	margin-left: auto;
	margin-right: auto;
}


.main{
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: center;
	padding-top: 100px;
	padding-bottom: 100px;
	width: 100%;
	height: auto;
	border: 3px solid black;
	overflow: hidden;
	position: relative;
	background: white;
	/* color: white; */
	border-radius: 10px;
}



.above{
	z-index: 2;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	text-align: center;
}
.dubyk{
	position: absolute;
	bottom: -50px;
	left: -60px;
	transform: rotate( 30deg);
}

@media (max-width: 800px) {
	.dubyk{
		position: static;
		transform: none;
		margin-top: 50px;
		margin-right: 30px;
	}

	.main{
		padding-bottom: 0;
		padding-top: 75px;
	}
}

#title{
	position: absolute;
	top: 75px;
	left: 50px;
	transform: rotate(-30deg);
}

.row{
	display: flex;
	flex-direction: row;
	align-items: center;
}

.textInput{
	border: 1px solid black;
	/* margin-top: 1px; */
	margin-left: 10px;
	margin-right: 10px;
	padding: 2px 5px 2px 5px;
}

.playBtn{
	cursor: pointer;
	background-color: transparent;
	border: none;
	transition: all 400ms;
}

.playBtn:hover{
	transform: scale(1.1);
}

